* {
    box-sizing: border-box;
}

body {
    color: lightgray;
    background-color: black;
}

header {
    display: flex;
    flex-direction: row;
    font-size: .75rem;
    align-items: center;
    padding: 20px 20px;
}

.header--image {
    width: 50px;
    max-width: 50px;
    height: 50px;
    filter: invert(100%);
}

.header--title {
    margin-left: 10px;
    margin-right: auto;
    font-family: Arial, Helvetica, sans-serif;
}

.main--title {
    text-align: center;
    font-family: 'Quicksand', sans-serif;
}

.main--sheep-jump {
    padding-top: 20px;
    padding-bottom: 40px;
}

.counter-display {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.main--sheep {
    width: 70px;
    filter: invert(80%);
    translate: 500% 110%;
}

.main--fence {
    width: 80px;
    filter: invert(70%);
    display: block;
    margin: 0 auto;
}

.main--buttons-container {
    display: grid;
    padding: 0 50px;
} 

.main--buttons {
    background-color: lightgray;
    color: black;
    padding: 20px;
    margin: 10px;
    border: none;
    border-radius: 7px;
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    font-size: 1rem;
    cursor: pointer;
}

.main--buttons:hover {
    filter: brightness(80%);
    transition: ease 250ms;
}

@media screen and (min-width: 500px){
    .main--sheep {
        translate: 900% 110%;
    }
}

@media screen and (min-width: 1025px){
    header {
    padding: 20px 300px;
    }

    .main--sheep-jump {
        padding-block: 100px;
        color: lightgray;
    }

    .main--title {
        font-size: 2.5rem;
    }
    
    .main--fence {
        width: 100px;
    }

    .main--sheep {
        translate: 2000% 110%;
    }

    .main--buttons-container {
        padding: 0 800px;
    }

}